<template>
  <el-form
    ref="pregnancyForm"
    :model="pregnancyForm"
    :rules="rules"
    label-width="110px"
    class="demo-pregnancyForm pregnancy-form"
  >
    <el-form-item label="分娩方式：" prop="fmfs">
      <el-radio-group v-model="pregnancyForm.fmfs">
        <el-radio label="自然分娩" />
        <el-radio label="剖宫产" />
      </el-radio-group>
      <el-form-item
        label="说明："
        prop="fmfsDescription"
        style="margin-top: 20px;"
        class="description"
      >
        <el-input
          v-model="pregnancyForm.fmfsDescription"
          placeholder="请输入"
          type="textarea"
          maxlength="500"
          show-word-limit
          resize="none"
          class="des-input"
        />
      </el-form-item>
    </el-form-item>
    <el-form-item label="羊水情况：" prop="ysqk">
      <el-radio-group v-model="pregnancyForm.ysqk">
        <el-radio label="清亮" />
        <el-radio label="浑浊" />
      </el-radio-group>
    </el-form-item>
    <el-form-item label="孕前体重：" prop="yqtz">
      <el-input
        v-model="pregnancyForm.yqtz"
        placeholder="请输入"
        @input="setNumber($event, 'yqtz')"
        @change="toFixedNum($event, 'yqtz')"
      />&nbsp;&nbsp;&nbsp;kg
    </el-form-item>
    <el-form-item label="分娩时体重：" prop="fmstz">
      <el-input
        v-model="pregnancyForm.fmstz"
        placeholder="请输入"
        @input="setNumber($event, 'fmstz')"
        @change="toFixedNum($event, 'fmstz')"
      />&nbsp;&nbsp;&nbsp;kg
    </el-form-item>
    <el-form-item label="孕期有无异常：" prop="yqywyc">
      <el-radio-group v-model="pregnancyForm.yqywyc">
        <el-radio label="无" />
        <el-radio label="有" />
      </el-radio-group>
      <el-form
        v-if="pregnancyForm.yqywyc === '有'"
        :model="pregnancyForm"
        :rules="rules"
        label-width="90px"
        class="demo-pregnancyForm pregnancy-form"
      >
        <el-form-item label="孕期产检：" prop="yqcj" class="m-b-20">
          <el-radio-group v-model="pregnancyForm.yqcj">
            <el-radio label="正常" />
            <el-radio label="异常" />
          </el-radio-group>
          <el-form-item
            v-if="pregnancyForm.yqcj === '异常'"
            label="说明："
            prop="yqcjDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="pregnancyForm.yqcjDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="贫血：" prop="anemia" class="m-b-20">
          <el-radio-group v-model="pregnancyForm.anemia">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="pregnancyForm.anemia === '有'"
            label="说明："
            prop="anemiaDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="pregnancyForm.anemiaDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="并发症：" prop="bfz">
          <el-checkbox-group v-model="pregnancyForm.bfz">
            <el-checkbox label="妊娠期糖尿病" name="rmqtnb" />
            <el-checkbox label="妊娠期高血压" name="rmqgxy" />
            <el-checkbox label="甲状腺功能亢进症" name="jzxgnkjz" />
            <el-checkbox label="甲状腺功能减退症" name="jzxgnjtz" />
            <el-checkbox label="妊娠期合并胆汁淤积症" name="dzyjz" />
            <el-checkbox name="other">
              其它
              <el-input v-model="pregnancyForm.bfzOther" />
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="药物干预：" prop="ywgy" class="m-b-20">
          <el-radio-group v-model="pregnancyForm.ywgy">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="pregnancyForm.ywgy === '有'"
            label="说明："
            prop="ywgyDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="pregnancyForm.ywgyDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="接触有害物质：" prop="jcyhwz" class="m-b-20">
          <el-radio-group v-model="pregnancyForm.jcyhwz">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="pregnancyForm.jcyhwz === '有'"
            label="说明："
            prop="jcyhwzDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="pregnancyForm.jcyhwzDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item
          label="其它："
          prop="yqywycOther"
          style="margin-top: 20px;max-width: 970px;"
          class="m-b-20"
        >
          <el-input
            v-model="pregnancyForm.yqywycOther"
            placeholder="请输入"
            type="textarea"
            maxlength="500"
            show-word-limit
            resize="none"
            class="des-input"
          />
        </el-form-item>
      </el-form>
    </el-form-item>
    <el-form-item v-if="!babyId || isMyUser" class="confirm">
      <el-button type="primary" @click="submitForm('pregnancyForm')"
        >修改并保存</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
import { updateHealthFile } from '@/api/healthManagement'
export default {
  props: {
    pregnancy: {
      type: [Object, String],
      default: ''
    },
    babyId: {
      type: Boolean,
      default: false
    },
    isMyUser: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pregnancyForm: {
        fmfs: '',
        fmfsDescription: '',
        ysqk: '',
        yqtz: '',
        fmstz: '',
        yqywyc: '',
        yqcj: '',
        yqcjDescription: '',
        anemia: '',
        anemiaDescription: '',
        bfz: [],
        bfzOther: '',
        ywgy: '',
        ywgyDescription: '',
        jcyhwz: '',
        jcyhwzDescription: '',
        yqywycOther: ''
      },
      rules: {}
    }
  },
  watch: {
    pregnancy() {
      if (this.pregnancy) {
        this.pregnancyForm = {
          fmfs: this.pregnancy.fmfs || '',
          fmfsDescription: this.pregnancy.fmfsDescription || '',
          ysqk: this.pregnancy.ysqk || '',
          yqtz: this.pregnancy.yqtz || '',
          fmstz: this.pregnancy.fmstz || '',
          yqywyc: this.pregnancy.yqywyc || '',
          yqcj: this.pregnancy.yqcj || '',
          yqcjDescription: this.pregnancy.yqcjDescription || '',
          anemia: this.pregnancy.anemia || '',
          anemiaDescription: this.pregnancy.anemiaDescription || '',
          bfz: this.pregnancy.bfz || [],
          bfzOther: this.pregnancy.bfzOther || '',
          ywgy: this.pregnancy.ywgy || '',
          ywgyDescription: this.pregnancy.ywgyDescription || '',
          jcyhwz: this.pregnancy.jcyhwz || '',
          jcyhwzDescription: this.pregnancy.jcyhwzDescription || '',
          yqywycOther: this.pregnancy.yqywycOther || ''
        }
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (this.pregnancyForm.yqywyc === '无') {
            this.pregnancyForm.yqcj = ''
            this.pregnancyForm.yqcjDescription = ''
            this.pregnancyForm.anemia = ''
            this.pregnancyForm.anemiaDescription = ''
            this.pregnancyForm.bfz = []
            this.pregnancyForm.bfzOther = ''
            this.pregnancyForm.ywgy = ''
            this.pregnancyForm.ywgyDescription = ''
            this.pregnancyForm.jcyhwz = ''
            this.pregnancyForm.jcyhwzDescription = ''
            this.pregnancyForm.yqywycOther = ''
          }
          if (this.pregnancyForm.yqcj === '正常') {
            this.pregnancyForm.yqcjDescription = ''
          }
          if (this.pregnancyForm.anemia === '无') {
            this.pregnancyForm.anemiaDescription = ''
          }
          if (this.pregnancyForm.ywgy === '无') {
            this.pregnancyForm.ywgyDescription = ''
          }
          if (this.pregnancyForm.jcyhwz === '无') {
            this.pregnancyForm.jcyhwzDescription = ''
          }
          const { data, code } = await updateHealthFile({
            baby_id:
              this.$route.params.id || this.$store.state.chat.activedBabyId,
            mather_pregnancy_info: this.pregnancyForm
          })
          if (code === 20000) {
            if (data.result) {
              this.$message({ type: 'success', message: '保存成功' })
            }
          } else this.$message({ message: data.message, type: 'error' })
        } else {
          return false
        }
      })
    },
    setNumber(value, type) {
      if (!Number(value)) this.pregnancyForm[type] = ''
    },
    toFixedNum(value, type) {
      if (!value) return
      if (Number(value)) this.pregnancyForm[type] = Number(value).toFixed(1)
    }
  }
}
</script>

<style scoped lang="scss">
.pregnancy-form {
  .m-b-20 {
    margin-bottom: 20px;
  }
  .description {
    max-width: 860px;
    /deep/.el-form-item__label {
      width: 60px !important;
    }
    /deep/.el-form-item__content {
      margin-left: 60px !important;
    }
  }
  .des-input {
    height: 140px;
    /deep/.el-textarea__inner {
      max-width: 800px;
      height: 140px;
    }
  }
  .confirm {
    text-align: center;
    /deep/.el-form-item__content {
      margin-left: 0 !important;
    }
    /deep/.el-button {
      padding: 0 12px;
      height: 32px;
    }
  }
}
</style>
