<template>
  <el-form
    ref="newbornForm"
    :model="newbornForm"
    :rules="rules"
    label-width="110px"
    class="demo-newbornForm newborn-form"
  >
    <el-form-item v-if="!babyId" label="宝宝乳名：" prop="name">
      <el-input
        v-model="newbornForm.name"
        maxlength="5"
        show-word-limit
        placeholder="请输入"
      />
    </el-form-item>
    <el-form-item label="宝宝性别：" prop="gender">
      <el-radio-group v-model="newbornForm.gender">
        <el-radio label="1">男</el-radio>
        <el-radio label="2">女</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="民族：" prop="famous">
      <el-input
        v-model="newbornForm.famous"
        maxlength="10"
        show-word-limit
        placeholder="请输入"
      />
    </el-form-item>
    <el-form-item label="出生日期：" prop="birthday">
      <el-input v-model="newbornForm.birthday" disabled />
    </el-form-item>
    <el-form-item label="宝宝月龄：" prop="monthAge">
      <el-input v-model="newbornForm.monthAge" disabled />
    </el-form-item>
    <el-form-item label="预产期：" prop="pregnancy_ended_at">
      <el-date-picker
        v-model="newbornForm.pregnancy_ended_at"
        type="date"
        placeholder="选择日期"
        @change="getAgeMonth"
      />
    </el-form-item>
    <el-form-item label="出生孕周：" prop="csyz">
      <el-input
        v-model="newbornForm.gestational_weeks"
        placeholder="请输入0-42的整数"
        @input="getNewNum($event, 'gestational_weeks')"
        @keyup="value => value.replace(/^(0+)|[^\d]+/g, '')"
      />&nbsp;&nbsp;&nbsp;周&nbsp;&nbsp;&nbsp;
      <el-input
        v-model="newbornForm.gestational_days"
        placeholder="请输入0-6的整数"
        @input="getNewNum($event, 'gestational_days')"
        @keyup="value => value.replace(/^(0+)|[^\d]+/g, '')"
      />&nbsp;&nbsp;&nbsp;天
    </el-form-item>
    <el-form-item label="是否早产：" prop="sfzc">
      <el-radio-group v-model="newbornForm.sfzc">
        <el-radio label="否" />
        <el-radio label="是" />
      </el-radio-group>
      <el-form
        v-if="newbornForm.sfzc === '是'"
        :model="newbornForm"
        :rules="rules"
        label-width="180px"
        class="demo-newbornForm"
        style="margin-top: 20px;"
      >
        <el-form-item label="矫正月龄：" prop="jzyl" class="m-b-20">
          <el-input v-model="newbornForm.jzyl" disabled />
        </el-form-item>
        <el-form-item label="住院天数：" prop="zyts" class="m-b-20">
          <el-input
            v-model="newbornForm.zyts"
            placeholder="请输入自然数"
            @input="getNewNum($event, 'zyts')"
            @keyup="value => value.replace(/^(0+)|[^\d]+/g, '')"
          />
        </el-form-item>
        <el-form-item label="住院时是否静脉营养：" prop="sfjmyy" class="m-b-20">
          <el-radio-group v-model="newbornForm.sfjmyy">
            <el-radio label="否" />
            <el-radio label="是" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.sfjmyy === '是'"
            label="说明："
            prop="sfjmyyDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.sfjmyyDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="是否使用呼吸机：" prop="sfsyhxj" class="m-b-20">
          <el-radio-group v-model="newbornForm.sfsyhxj">
            <el-radio label="否" />
            <el-radio label="是" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.sfsyhxj === '是'"
            label="说明："
            prop="sfsyhxjDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.sfsyhxjDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item
          label="眼底筛查结果："
          prop="ydscjg"
          class="m-b-20"
          style="max-width: 980px;"
        >
          <el-input
            v-model="newbornForm.ydscjg"
            placeholder="请输入"
            type="textarea"
            maxlength="500"
            show-word-limit
            resize="none"
            class="des-input"
          />
          <div class="upload-image">
            <div v-if="newbornForm.imageUrl" class="img">
              <img
                width="100%"
                height="100%"
                :src="newbornForm.imageUrl"
                alt="筛查结果"
              />
              <i class="el-icon-error" @click="newbornForm.imageUrl = ''" />
            </div>
            <div v-else class="img-box">
              <i class="el-icon-circle-plus-outline" />
              <p>上传图片</p>
            </div>
          </div>
          <el-upload
            class="upload-demo"
            action=""
            :http-request="uploadFunc"
            :before-upload="beforeAvatarUpload"
            :show-file-list="false"
          >
            <el-button size="small" type="primary">上传图片</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item
          label="晶状体后纤维增生性眼病："
          prop="jzthxwzsxyb"
          class="m-b-20"
        >
          <el-radio-group v-model="newbornForm.jzthxwzsxyb">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
        </el-form-item>
        <el-form-item
          label="其它："
          prop="sfzcOther"
          style="margin-top: 20px;max-width: 980px;"
        >
          <el-input
            v-model="newbornForm.sfzcOther"
            placeholder="请输入"
            type="textarea"
            maxlength="500"
            show-word-limit
            resize="none"
            class="des-input"
          />
        </el-form-item>
      </el-form>
    </el-form-item>
    <el-form-item label="宝宝胎数：" prop="bbts">
      <el-radio-group v-model="newbornForm.bbts">
        <el-radio label="一胎" />
        <el-radio label="二胎" />
        <el-radio label="多胎" />
      </el-radio-group>
    </el-form-item>
    <el-form-item label="出生体重：" prop="cstz">
      <el-input
        v-model="newbornForm.cstz"
        placeholder="请输入"
        @input="setNumber($event, 'tz')"
        @blur="toFixedNum($event, 'tz')"
        @change="toFixedNum($event, 'tz')"
      />&nbsp;&nbsp;&nbsp;kg
    </el-form-item>
    <el-form-item label="出生身长：" prop="cssc">
      <el-input
        v-model="newbornForm.cssc"
        placeholder="请输入"
        @input="setNumber($event, 'sc')"
        @blur="toFixedNum($event, 'tz')"
        @change="toFixedNum($event, 'sc')"
      />&nbsp;&nbsp;&nbsp;cm
    </el-form-item>
    <el-form-item label="出身头围：" prop="cstw">
      <el-input
        v-model="newbornForm.cstw"
        placeholder="请输入"
        @input="setNumber($event, 'tw')"
        @blur="toFixedNum($event, 'tz')"
        @change="toFixedNum($event, 'tw')"
      />&nbsp;&nbsp;&nbsp;cm
    </el-form-item>
    <el-form-item label="出生有无异常：" prop="csywyc">
      <el-radio-group v-model="newbornForm.csywyc">
        <el-radio label="无" />
        <el-radio label="有" />
      </el-radio-group>
      <el-form
        v-if="newbornForm.csywyc === '有'"
        :model="newbornForm"
        :rules="rules"
        label-width="170px"
        class="demo-newbornForm"
        style="margin-top: 20px;"
      >
        <el-form-item label="Apgar评分：" prop="apgar" class="m-b-20">
          <p class="minute">
            1分钟&nbsp;&nbsp;&nbsp;<el-input
              v-model="newbornForm.apgar.one"
              placeholder="请输入正整数"
              @input="getInteger($event, 'one')"
              @keyup="value => value.replace(/^(0+)|[^\d]+/g, '')"
            />
          </p>
          <p class="minute">
            5分钟&nbsp;&nbsp;&nbsp;<el-input
              v-model="newbornForm.apgar.five"
              placeholder="请输入正整数"
              @input="getInteger($event, 'five')"
              @keyup="value => value.replace(/^(0+)|[^\d]+/g, '')"
            />
          </p>
          <p class="minute">
            10分钟&nbsp;&nbsp;&nbsp;<el-input
              v-model="newbornForm.apgar.ten"
              placeholder="请输入正整数"
              @input="getInteger($event, 'ten')"
              @keyup="value => value.replace(/^(0+)|[^\d]+/g, '')"
            />
          </p>
        </el-form-item>
        <el-form-item label="生后窒息：" prop="shzx" class="m-b-20">
          <el-radio-group v-model="newbornForm.shzx">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.shzx === '有'"
            label="程度："
            prop="shzxcd"
            class="description"
            style="margin-top: 20px;"
          >
            <el-radio-group v-model="newbornForm.shzxcd">
              <el-radio label="轻" />
              <el-radio label="中" />
              <el-radio label="重度" />
            </el-radio-group>
          </el-form-item>
        </el-form-item>
        <el-form-item label="颅内出血：" prop="lncx" class="m-b-20">
          <el-radio-group v-model="newbornForm.lncx">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.lncx === '有'"
            label="程度："
            prop="lncxcd"
            class="description"
            style="margin-top: 20px;"
          >
            <el-radio-group v-model="newbornForm.lncxcd">
              <el-radio label="轻" />
              <el-radio label="中" />
              <el-radio label="重度" />
            </el-radio-group>
          </el-form-item>
        </el-form-item>
        <el-form-item label="宫内窘迫：" prop="gnjp" class="m-b-20">
          <el-radio-group v-model="newbornForm.gnjp">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
        </el-form-item>
        <el-form-item label="新生儿黄疸：" prop="xsehd" class="m-b-20">
          <el-radio-group v-model="newbornForm.xsehd">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <template v-if="newbornForm.xsehd === '有'">
            <el-form-item
              label="诊断方法："
              prop="hdzdff"
              style="margin-top: 20px;max-width: 980px;"
              class="description-way"
            >
              <el-input
                v-model="newbornForm.hdzdff"
                placeholder="请输入"
                type="textarea"
                maxlength="500"
                show-word-limit
                resize="none"
                class="des-input"
              />
            </el-form-item>
            <el-form-item
              label="治疗方法："
              prop="hdzlff"
              style="margin-top: 20px;max-width: 980px;"
              class="description-way"
            >
              <el-input
                v-model="newbornForm.hdzlff"
                placeholder="请输入"
                type="textarea"
                maxlength="500"
                show-word-limit
                resize="none"
                class="des-input"
              />
            </el-form-item>
          </template>
        </el-form-item>
        <el-form-item label="呼吸窘迫综合征(RDS)：" prop="RDS" class="m-b-20">
          <el-radio-group v-model="newbornForm.RDS">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
        </el-form-item>
        <el-form-item label="贫血：" prop="anemia" class="m-b-20">
          <el-radio-group v-model="newbornForm.anemia">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.anemia === '有'"
            label="说明："
            prop="anemiaDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.anemiaDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="惊厥：" prop="horror" class="m-b-20">
          <el-radio-group v-model="newbornForm.horror">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.horror === '有'"
            label="说明："
            prop="horrorDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.horrorDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="感染：" prop="infection" class="m-b-20">
          <el-radio-group v-model="newbornForm.infection">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.infection === '有'"
            label="说明："
            prop="infectionDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.infectionDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="出生缺陷：" prop="csqx" class="m-b-20">
          <el-radio-group v-model="newbornForm.csqx">
            <el-radio label="无" />
            <el-radio label="有" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.csqx === '有'"
            label="说明："
            prop="csqxDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.csqxDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="听力筛查：" prop="tlsc" class="m-b-20">
          <el-radio-group v-model="newbornForm.tlsc">
            <el-radio label="正常" />
            <el-radio label="异常" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.tlsc === '异常'"
            label="说明："
            prop="tlscDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.tlscDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item label="新生儿筛查(足跟血)：" prop="zgx" class="m-b-20">
          <el-radio-group v-model="newbornForm.zgx">
            <el-radio label="正常" />
            <el-radio label="异常" />
          </el-radio-group>
          <el-form-item
            v-if="newbornForm.zgx === '异常'"
            label="说明："
            prop="zgxDescription"
            style="margin-top: 20px;"
            class="description"
          >
            <el-input
              v-model="newbornForm.zgxDescription"
              placeholder="请输入"
              type="textarea"
              maxlength="500"
              show-word-limit
              resize="none"
              class="des-input"
            />
          </el-form-item>
        </el-form-item>
        <el-form-item
          label="其它："
          prop="cswycOther"
          style="margin-top: 20px;max-width: 970px;"
          class="m-b-20"
        >
          <el-input
            v-model="newbornForm.cswycOther"
            placeholder="请输入"
            type="textarea"
            maxlength="500"
            show-word-limit
            resize="none"
            class="des-input"
          />
        </el-form-item>
      </el-form>
    </el-form-item>
    <el-form-item label="喂养方式：" prop="wyfs">
      <el-checkbox-group v-model="newbornForm.wyfs">
        <el-checkbox label="母乳" name="mr" />
        <el-checkbox label="母乳+强化剂" name="mrqhj" />
        <el-checkbox label="配方奶粉" name="pfnf" />
        <el-checkbox label="混合喂养" name="hhwy" />
        <el-checkbox label="辅食" name="fs" />
      </el-checkbox-group>
      <el-form-item
        label="说明："
        prop="wyfsDescription"
        style="margin-top: 20px;"
        class="description"
      >
        <el-input
          v-model="newbornForm.wyfsDescription"
          placeholder="请输入"
          type="textarea"
          maxlength="500"
          show-word-limit
          resize="none"
          class="des-input"
        />
      </el-form-item>
    </el-form-item>
    <el-form-item label="疫苗接种：" prop="ymjz">
      <el-radio-group v-model="newbornForm.ymjz">
        <el-radio label="按时" />
        <el-radio label="未按时" />
      </el-radio-group>
      <el-form-item
        label="请上传疫苗本："
        prop="uploadVaccine"
        style="margin-top: 20px;"
      >
        <div class="upload-image-list">
          <div
            v-for="(item, index) of newbornForm.imageUrlList"
            :key="index"
            class="img"
          >
            <img width="100%" height="100%" :src="item" alt="疫苗本" />
            <i class="el-icon-error" @click="removeImg(index)" />
          </div>
          <div class="img-box">
            <i class="el-icon-circle-plus-outline" />
            <p>最多10张</p>
          </div>
        </div>
        <el-upload
          class="upload-demo"
          action=""
          :http-request="uploadListFunc"
          :before-upload="beforeAvatarUpload"
          :show-file-list="false"
        >
          <el-button
            size="small"
            type="primary"
            :disabled="newbornForm.imageUrlList.length >= 10"
            >上传图片</el-button
          >
        </el-upload>
        <el-form-item
          v-if="newbornForm.ymjz === '未按时'"
          label="说明："
          prop="ymjzDescription"
          style="margin-top: 20px;"
          class="description"
        >
          <el-input
            v-model="newbornForm.ymjzDescription"
            placeholder="请输入"
            type="textarea"
            maxlength="500"
            show-word-limit
            resize="none"
            class="des-input"
          />
        </el-form-item>
      </el-form-item>
    </el-form-item>
    <el-form-item v-if="!babyId || isMyUser" class="confirm">
      <el-button type="primary" @click="submitForm('newbornForm')"
        >修改并保存</el-button
      >
    </el-form-item>
  </el-form>
</template>
<script>
import { upload } from '@/api/upload'
import { updateHealthFile, getAgeMonth } from '@/api/healthManagement'
export default {
  props: {
    newborn: {
      type: [Object, String],
      default: ''
    },
    babyInfo: {
      type: [Object, String],
      default: ''
    },
    babyId: {
      type: Boolean,
      default: false
    },
    isMyUser: {
      type: Boolean,
      default: false
    },
    isChatPage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      newbornForm: {
        name: '',
        gender: '',
        famous: '',
        birthday: '',
        monthAge: '',
        pregnancy_ended_at: '',
        gestational_weeks: '',
        gestational_days: '',
        sfzc: '', // 是否早产
        jzyl: '',
        zyts: '',
        sfjmyy: '',
        sfjmyyDescription: '',
        sfsyhxj: '',
        sfsyhxjDescription: '',
        ydscjg: '',
        jzthxwzsxyb: '',
        sfzcOther: '',
        bbts: '',
        cstz: '',
        cssc: '',
        cstw: '',
        csywyc: '', // 出生有无异常
        apgar: {
          one: '',
          five: '',
          ten: ''
        },
        shzx: '',
        shzxcd: '',
        lncx: '',
        lncxcd: '',
        gnjp: '',
        xsehd: '',
        hdzdff: '',
        hdzlff: '',
        RDS: '',
        anemia: '',
        anemiaDescription: '',
        horror: '',
        horrorDescription: '',
        infection: '',
        infectionDescription: '',
        csqx: '',
        csqxDescription: '',
        tlsc: '',
        tlscDescription: '',
        zgx: '',
        zgxDescription: '',
        cswycOther: '',
        wyfs: [],
        wyfsDescription: '',
        ymjz: '',
        ymjzDescription: '',
        imageUrl: '',
        imageUrlList: []
      },
      rules: {
        name: [{ required: true, message: '请输入宝宝姓名', trigger: 'blur' }],
        gender: [{ required: true, message: '请选择性别', trigger: 'change' }]
      }
    }
  },
  watch: {
    newborn() {
      if (this.newborn) {
        this.newbornForm = {
          name: this.newborn.name || '',
          gender: this.newborn.gender || '',
          famous: this.newborn.famous || '',
          birthday: this.newborn.birthday || '',
          monthAge: this.newborn.monthAge || '',
          pregnancy_ended_at: this.newborn.pregnancy_ended_at || '',
          gestational_weeks:
            this.babyInfo.gestational_weeks || this.newborn.gestational_weeks,
          gestational_days:
            this.babyInfo.gestational_days || this.newborn.gestational_days,
          sfzc: this.newborn.sfzc || '', // 是否早产
          jzyl: this.newborn.jzyl || '',
          zyts: this.newborn.zyts || '',
          sfjmyy: this.newborn.sfjmyy || '',
          sfjmyyDescription: this.newborn.sfjmyyDescription || '',
          sfsyhxj: this.newborn.sfsyhxj || '',
          sfsyhxjDescription: this.newborn.sfsyhxjDescription || '',
          ydscjg: this.newborn.ydscjg || '',
          jzthxwzsxyb: this.newborn.jzthxwzsxyb || '',
          sfzcOther: this.newborn.sfzcOther || '',
          bbts: this.newborn.bbts || '',
          cstz: this.newborn.cstz || '',
          cssc: this.newborn.cssc || '',
          cstw: this.newborn.cstw || '',
          csywyc: this.newborn.csywyc || '', // 出生有无异常
          apgar: {
            one: this.newborn.apgar ? this.newborn.apgar.one : '',
            five: this.newborn.apgar ? this.newborn.apgar.five : '',
            ten: this.newborn.apgar ? this.newborn.apgar.ten : ''
          },
          shzx: this.newborn.shzx || '',
          shzxcd: this.newborn.shzxcd || '',
          lncx: this.newborn.lncx || '',
          lncxcd: this.newborn.lncxcd || '',
          gnjp: this.newborn.gnjp || '',
          xsehd: this.newborn.xsehd || '',
          hdzdff: this.newborn.hdzdff || '',
          hdzlff: this.newborn.hdzlff || '',
          RDS: this.newborn.RDS || '',
          anemia: this.newborn.anemia || '',
          anemiaDescription: this.newborn.anemiaDescription || '',
          horror: this.newborn.horror || '',
          horrorDescription: this.newborn.horrorDescription || '',
          infection: this.newborn.infection || '',
          infectionDescription: this.newborn.infectionDescription || '',
          csqx: this.newborn.csqx || '',
          csqxDescription: this.newborn.csqxDescription || '',
          tlsc: this.newborn.tlsc || '',
          tlscDescription: this.newborn.tlscDescription || '',
          zgx: this.newborn.zgx || '',
          zgxDescription: this.newborn.zgxDescription || '',
          cswycOther: this.newborn.cswycOther || '',
          wyfs: this.newborn.wyfs || [],
          wyfsDescription: this.newborn.wyfsDescription || '',
          ymjz: this.newborn.ymjz || '',
          ymjzDescription: this.newborn.ymjzDescription || '',
          imageUrl: this.newborn.imageUrl || '',
          imageUrlList: this.newborn.imageUrlList || []
        }
      }
      if (this.newbornForm.pregnancy_ended_at) {
        this.getAgeMonth({ day: this.newbornForm.pregnancy_ended_at })
      }
    },
    babyInfo() {
      if (this.babyInfo) {
        this.newbornForm.birthday = this.babyInfo.birthday
          ? this.formatTime(this.babyInfo.birthday, '{y}-{m}-{d}')
          : ''
        this.newbornForm.monthAge = this.babyInfo.age_of_month
        this.newbornForm.name = this.babyInfo.name
        this.newbornForm.gender = this.babyInfo.gender + ''
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (this.newbornForm.sfzc === '否') {
            this.newbornForm.jzyl = ''
            this.newbornForm.zyts = ''
            this.newbornForm.sfjmyy = ''
            this.newbornForm.sfjmyyDescription = ''
            this.newbornForm.sfsyhxj = ''
            this.newbornForm.sfsyhxjDescription = ''
            this.newbornForm.ydscjg = ''
            this.newbornForm.jzthxwzsxyb = ''
            this.newbornForm.sfzcOther = ''
            this.newbornForm.imageUrl = ''
          }
          if (this.newbornForm.sfjmyy === '否') {
            this.newbornForm.sfjmyyDescription = ''
          }
          if (this.newbornForm.sfsyhxj === '否') {
            this.newbornForm.sfsyhxjDescription = ''
          }
          if (this.newbornForm.csywyc === '无') {
            this.newbornForm.apgar = {
              one: '',
              five: '',
              ten: ''
            }
            this.newbornForm.shzx = ''
            this.newbornForm.shzxcd = ''
            this.newbornForm.lncx = ''
            this.newbornForm.lncxcd = ''
            this.newbornForm.gnjp = ''
            this.newbornForm.xsehd = ''
            this.newbornForm.hdzdff = ''
            this.newbornForm.hdzlff = ''
            this.newbornForm.RDS = ''
            this.newbornForm.anemia = ''
            this.newbornForm.anemiaDescription = ''
            this.newbornForm.horror = ''
            this.newbornForm.horrorDescription = ''
            this.newbornForm.infection = ''
            this.newbornForm.infectionDescription = ''
            this.newbornForm.csqx = ''
            this.newbornForm.csqxDescription = ''
            this.newbornForm.tlsc = ''
            this.newbornForm.tlscDescription = ''
            this.newbornForm.zgx = ''
            this.newbornForm.zgxDescription = ''
            this.newbornForm.cswycOther = ''
          }
          if (this.newbornForm.shzx === '无') this.newbornForm.shzxcd = ''
          if (this.newbornForm.lncx === '无') this.newbornForm.lncxcd = ''
          if (this.newbornForm.xsehd === '无') {
            this.newbornForm.hdzdff = ''
            this.newbornForm.hdzlff = ''
          }
          if (this.newbornForm.anemia === '无') {
            this.newbornForm.anemiaDescription = ''
          }
          if (this.newbornForm.horror === '无') {
            this.newbornForm.horrorDescription = ''
          }
          if (this.newbornForm.infection === '无') {
            this.newbornForm.infectionDescription = ''
          }
          if (this.newbornForm.csqx === '无') {
            this.newbornForm.csqxDescription = ''
          }
          if (this.newbornForm.tlsc === '异常') {
            this.newbornForm.tlscDescription = ''
          }
          if (this.newbornForm.ymjz === '未按时') {
            this.newbornForm.ymjzDescription = ''
          }
          const { data, code } = await updateHealthFile({
            baby_id:
              this.$route.params.id || this.$store.state.chat.activedBabyId,
            baby_detail_info: this.newbornForm
          })
          if (code === 20000) {
            if (data.result) {
              this.$message({ type: 'success', message: '保存成功' })
            }
          } else this.$message({ message: data.message, type: 'error' })
        } else {
          return false
        }
      })
    },
    setNumber(value, type) {
      if (type === 'tz') if (!Number(value)) this.newbornForm.cstz = ''
      if (type === 'sc') if (!Number(value)) this.newbornForm.cssc = ''
      if (type === 'tw') if (!Number(value)) this.newbornForm.cstw = ''
    },
    toFixedNum(value, type) {
      if (!value) return
      if (type === 'tz') {
        if (Number(value)) this.newbornForm.cstz = Number(value).toFixed(1)
      }
      if (type === 'sc') {
        if (Number(value)) this.newbornForm.cssc = Number(value).toFixed(1)
      }
      if (type === 'tw') {
        if (Number(value)) this.newbornForm.cstw = Number(value).toFixed(1)
      }
    },
    async uploadFunc(option) {
      if (
        this.newbornForm.imageUrlList.length >= 10 ||
        (!this.isMyUser && this.isChatPage)
      )
        return
      const data = await upload(option.file, progress => {})
      this.newbornForm.imageUrl = data.url
    },
    async uploadListFunc(option) {
      if (
        this.newbornForm.imageUrlList.length === 10 ||
        (!this.isMyUser && this.isChatPage)
      )
        return
      const data = await upload(option.file, progress => {})
      this.newbornForm.imageUrlList.push(data.url)
    },
    beforeAvatarUpload(file) {
      if (
        (this.isMyUser &&
          this.isChatPage &&
          this.newbornForm.imageUrlList.length < 10) ||
        (!this.isChatPage && this.newbornForm.imageUrlList.length < 10)
      ) {
        const isJPG =
          ['image/jpeg', 'image/jpg', 'image/png'].indexOf(file.type) >= 0
        const isLt5M = file.size / 1024 / 1024 < 5
        if (!isJPG) this.$message.error('上传图片只能是 JPG或PNG 格式!')
        if (!isLt5M) this.$message.error('上传图片大小不能超过 5MB!')
        return isJPG && isLt5M
      } else {
        return
      }
    },
    removeImg(i) {
      this.newbornForm.imageUrlList.splice(i, 1)
    },
    getNewNum(value, type) {
      if (parseInt(value) || parseInt(value) === 0) {
        if (type === 'gestational_weeks' && parseInt(value) > 42) {
          this.newbornForm[type] = 42
        } else if (type === 'gestational_days' && parseInt(value) > 6) {
          this.newbornForm[type] = 6
        } else this.newbornForm[type] = parseInt(value)
      } else this.newbornForm[type] = ''
    },
    getInteger(value, type) {
      if (type === 'zyts' && (parseInt(value) || parseInt(value) === 0)) {
        this.newbornForm.zyts = parseInt(value)
        return
      }
      if (parseInt(value) || parseInt(value) === 0) {
        this.newbornForm.apgar[type] = parseInt(value)
      } else this.newbornForm.apgar[type] = ''
    },
    async getAgeMonth() {
      const { code, data } = await getAgeMonth({
        day: this.newbornForm.pregnancy_ended_at
      })
      if (code === 20000) this.newbornForm.jzyl = data.age_of_month_str
    }
  }
}
</script>

