<template>
  <div class="personal-information">
    <el-form
      ref="personForm"
      :model="personForm"
      :rules="rules"
      label-width="130px"
      class="demo-personForm person-form"
    >
      <template v-for="(item, index) of personForm">
        <div :key="index">
          <h2 class="mother">{{ index === 'mother' ? '母亲' : '父亲' }}</h2>
          <el-form-item v-if="!babyId" label="姓名：" prop="name">
            <el-input
              v-model="item.name"
              maxlength="5"
              show-word-limit
              placeholder="请输入"
            />
          </el-form-item>
          <el-form-item label="出生日期：" prop="birthday">
            <el-date-picker
              v-model="item.birthday"
              type="date"
              placeholder="选择日期"
            />
          </el-form-item>
          <el-form-item label="民族：" prop="famous">
            <el-input
              v-model="item.famous"
              maxlength="10"
              show-word-limit
              placeholder="请输入"
            />
          </el-form-item>
          <el-form-item label="身高：" prop="height">
            <el-input
              v-model="item.height"
              placeholder="请输入"
              @input="setNumber($event, index, 'height')"
              @change="toFixedNum($event, index, 'height')"
            />&nbsp;&nbsp;&nbsp;kg
          </el-form-item>
          <el-form-item label="体重：" prop="bodyWeight">
            <el-input
              v-model="item.bodyWeight"
              placeholder="请输入"
              @input="setNumber($event, index, 'bodyWeight')"
              @change="toFixedNum($event, index, 'bodyWeight')"
            />&nbsp;&nbsp;&nbsp;kg
          </el-form-item>
          <el-form-item label="血型：" prop="bloodGroup">
            <el-input
              v-model="item.bloodGroup"
              placeholder="请输入"
            />&nbsp;&nbsp;&nbsp;型
          </el-form-item>
          <el-form-item label="现居住地：" prop="address">
            <el-cascader
              v-model="item.address"
              size="large"
              :options="options"
              @change="handleChange"
            />
          </el-form-item>
          <el-form-item label="家族遗传史：" prop="jzycs" class="m-b-20">
            <el-radio-group v-model="item.jzycs">
              <el-radio label="无" />
              <el-radio label="有" />
            </el-radio-group>
            <el-form-item
              v-if="item.jzycs === '有'"
              label="说明："
              prop="jzycsDescription"
              style="margin-top: 20px;"
              class="description"
            >
              <el-input
                v-model="item.jzycsDescription"
                placeholder="请输入"
                type="textarea"
                maxlength="500"
                show-word-limit
                resize="none"
                class="des-input"
              />
            </el-form-item>
          </el-form-item>
          <el-form-item label="既往史：" prop="jws" class="m-b-20">
            <el-radio-group v-model="item.jws">
              <el-radio label="无" />
              <el-radio label="有" />
            </el-radio-group>
            <el-form-item
              v-if="item.jws === '有'"
              label="说明："
              prop="jwsDescription"
              style="margin-top: 20px;"
              class="description"
            >
              <el-input
                v-model="item.jwsDescription"
                placeholder="请输入"
                type="textarea"
                maxlength="500"
                show-word-limit
                resize="none"
                class="des-input"
              />
            </el-form-item>
          </el-form-item>
          <el-form-item label="烟酒嗜好：" prop="yjsh" class="m-b-20">
            <el-radio-group v-model="item.yjsh">
              <el-radio label="无" />
              <el-radio label="有" />
            </el-radio-group>
            <el-form-item
              v-if="item.yjsh === '有'"
              label="说明："
              prop="yjshDescription"
              style="margin-top: 20px;"
              class="description"
            >
              <el-input
                v-model="item.yjshDescription"
                placeholder="请输入"
                type="textarea"
                maxlength="500"
                show-word-limit
                resize="none"
                class="des-input"
              />
            </el-form-item>
          </el-form-item>
        </div>
      </template>
      <el-form-item v-if="!babyId || isMyUser" class="confirm">
        <el-button type="primary" @click="submitForm('personForm')"
          >修改并保存</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { regionData } from 'element-china-area-data'
import { updateHealthFile } from '@/api/healthManagement'
export default {
  props: {
    person: {
      type: [Object, String],
      default: ''
    },
    babyId: {
      type: Boolean,
      default: false
    },
    isMyUser: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      personForm: {
        mother: {
          title: '母亲',
          name: '',
          birthday: '',
          famous: '',
          height: '',
          bodyWeight: '',
          bloodGroup: '',
          address: [],
          jzycs: '',
          jzycsDescription: '',
          jws: '',
          jwsDescription: '',
          yjsh: '',
          yjshDescription: ''
        },
        father: {
          title: '父亲',
          name: '',
          birthday: '',
          famous: '',
          height: '',
          bodyWeight: '',
          bloodGroup: '',
          address: [],
          jzycs: '',
          jzycsDescription: '',
          jws: '',
          jwsDescription: '',
          yjsh: '',
          yjshDescription: ''
        }
      },
      rules: {},
      options: [
        { label: '国外', value: '国外' },
        { label: '国内', value: '国内', children: regionData }
      ]
    }
  },
  watch: {
    person() {
      if (this.person.mother) {
        this.personForm.mother = this.person.mother
        this.personForm.father = this.person.father
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (this.personForm.mother.jzycs === '无') {
            this.personForm.mother.jzycsDescription = ''
          }
          if (this.personForm.mother.jws === '无') {
            this.personForm.mother.jwsDescription = ''
          }
          if (this.personForm.mother.yjsh === '无') {
            this.personForm.mother.yjshDescription = ''
          }
          if (this.personForm.father.jzycs === '无') {
            this.personForm.father.jzycsDescription = ''
          }
          if (this.personForm.father.jws === '无') {
            this.personForm.father.jwsDescription = ''
          }
          if (this.personForm.father.yjsh === '无') {
            this.personForm.father.yjshDescription = ''
          }
          const { data, code } = await updateHealthFile({
            baby_id:
              this.$route.params.id || this.$store.state.chat.activedBabyId,
            family_info: this.personForm
          })
          if (code === 20000) {
            if (data.result) {
              this.$message({ type: 'success', message: '保存成功' })
            }
          } else this.$message({ message: data.message, type: 'error' })
        } else {
          return false
        }
      })
    },
    handleChange(value) {},
    setNumber(value, i, type) {
      if (!Number(value)) this.personForm[i][type] = ''
    },
    toFixedNum(value, i, type) {
      if (!value) return
      if (Number(value)) this.personForm[i][type] = Number(value).toFixed(1)
    }
  }
}
</script>

<style scoped lang="scss">
.person-form {
  .m-b-20 {
    margin-bottom: 20px;
  }
  .description {
    max-width: 860px;
    /deep/.el-form-item__label {
      width: 60px !important;
    }
    /deep/.el-form-item__content {
      margin-left: 60px !important;
    }
  }
  .des-input {
    height: 140px;
    /deep/.el-textarea__inner {
      max-width: 800px;
      height: 140px;
    }
  }
  .confirm {
    text-align: center;
    /deep/.el-form-item__content {
      margin-left: 0 !important;
    }
    /deep/.el-button {
      padding: 0 12px;
      height: 32px;
    }
  }
  .mother {
    margin-bottom: 20px;
  }
}
</style>
