<template>
  <div class="file-detail">
    <div v-if="!babyId || isChatPage" class="baby-info">
      <el-avatar :size="32" :src="babyInfo.user && babyInfo.avatar" />
      <span class="user-name"
        >用户： {{ babyInfo.user.name }} &nbsp;<svg-icon
          v-if="replace"
          icon-class="replace"
      /></span>
      <span class="baby-name">宝宝： {{ babyInfo.name }}</span>
    </div>
    <el-tabs v-model="activeName" type="card">
      <el-tab-pane label="新生儿基本信息" name="first">
        <newborn
          :isMyUser="isMyUser"
          :isChatPage="isChatPage"
          :newborn="newborn"
          :baby-info="babyInfo"
          :baby-id="!!babyId"
        />
      </el-tab-pane>
      <el-tab-pane label="母亲孕期情况" name="second">
        <pregnancy-situation
          :isMyUser="isMyUser"
          :pregnancy="pregnancy"
          :baby-id="!!babyId"
        />
      </el-tab-pane>
      <el-tab-pane label="家庭个人资料" name="third">
        <personal-information
          :isMyUser="isMyUser"
          :person="person"
          :baby-id="!!babyId"
        />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import Newborn from './components/newborn'
import PersonalInformation from './components/personalInformation'
import PregnancySituation from './components/pregnancySituation'
import { getHealthFile } from '@/api/healthManagement'
import { mapGetters } from 'vuex'
export default {
  components: {
    Newborn,
    PersonalInformation,
    PregnancySituation
  },
  computed: {
    ...mapGetters(['activedBabyId', 'isMyUser'])
  },
  props: {
    babyId: {
      type: String,
      default: ''
    },
    isChatPage: {
      type: Boolean,
      default: false
    },
    getInfo: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      activeName: 'first',
      replace: false,
      newborn: {},
      pregnancy: {},
      person: {},
      babyInfo: {
        user: {}
      }
    }
  },
  watch: {
    babyId() {
      this.getHealthFile()
    }
  },
  created() {
    this.getHealthFile()
  },
  methods: {
    async getHealthFile() {
      if (!this.getInfo && !this.babyId) return
      const { data, code } = await getHealthFile({
        baby: this.babyId || this.$route.params.id || this.activedBabyId
      })
      if (code === 20000) {
        this.newborn = data.baby_detail_info
        this.pregnancy = data.mather_pregnancy_info
        this.person = data.family_info
        this.babyInfo = data.family_member
        this.replace = data.is_replace
      }
    }
  }
}
</script>

<style scoped lang="scss">
.file-detail {
  overflow-x: auto;
  margin-right: 10px;
  min-width: 560px;
  max-width: 600px;

  .baby-info {
    display: flex;
    line-height: 32px;
    font-weight: 500;
    margin-bottom: 30px;
    /deep/.el-avatar {
      margin-right: 10px;
    }
    .baby-name {
      margin-left: 30px;
    }
  }
  /deep/.is-active {
    color: #fff;
    background-color: #409eff;
    border-radius: 2px;
    border-bottom: none;
  }
  /deep/.el-tabs__nav {
    border: 1px solid #e4e7ed;
  }
  /deep/.el-tabs__header {
    border-bottom: none;
    margin-bottom: 30px;
  }
  /deep/.el-input {
    width: 86.5%;
    height: 32px;
    .el-input__inner {
      height: 32px;
      line-height: 32px;
    }
  }
}
</style>
